import { useRef, useEffect, useState } from "react";
import { motion } from "framer-motion";
import { client } from "../api/client";
import { useAuthStore } from "../store/authStore";

export function LoginPage() {
  const containerRef = useRef<HTMLDivElement>(null);
  const authRenderedRef = useRef(false);
  const [isReady, setIsReady] = useState(false);
  const { setUser } = useAuthStore();

  // 
  useEffect(() => {
    client.auth.getSession().then((session) => {
      if (session.data?.user) {
        setUser({
          id: session.data.user.id,
          email: session.data.user.email,
          name: session.data.user.name || "",
        });
      } else {
        setIsReady(true);
      }
    });
  }, [setUser]);

  // UI
  useEffect(() => {
    if (!isReady || authRenderedRef.current || !containerRef.current) return;
    authRenderedRef.current = true;

    client.auth.renderAuthUI(containerRef.current, {
      redirectTo: "/",
      onLogin: (user) => {
        setUser({
          id: user.id,
          email: user.email,
          name: user.name || "",
        });
      },
      labels: {
        signIn: {
          title: "",
          subtitle: "",
          emailPlaceholder: "",
          passwordPlaceholder: "",
          loginButton: "",
          forgotPassword: "",
          signUpPrompt: "",
          toggleToSignUp: "",
        },
        signUp: {
          title: "",
          subtitle: "",
          namePlaceholder: "",
          emailPlaceholder: "",
          passwordPlaceholder: "",
          signUpButton: "",
          signInPrompt: "",
          toggleToSignIn: "",
        },
      },
    });
  }, [isReady, setUser]);

  if (!isReady) {
    return (
      <div className="h-dvh w-full flex items-center justify-center bg-canvas paper-texture">
        <motion.div
          className="flex flex-col items-center gap-4"
          animate={{ opacity: [0.5, 1, 0.5] }}
          transition={{ duration: 2, repeat: Infinity }}
        >
          <div className="w-16 h-16 rounded-full bg-gradient-to-br from-mood-melancholy/30 to-mood-warmth/30 blur-lg" />
          <p className="text-ink-faint font-serif text-sm">...</p>
        </motion.div>
      </div>
    );
  }

  return (
    <div className="h-dvh w-full flex flex-col items-center justify-center bg-canvas paper-texture overflow-hidden">
      {/*  */}
      <div className="absolute inset-0 pointer-events-none">
        <motion.div
          className="absolute w-96 h-96 rounded-full opacity-20"
          style={{
            background:
              "radial-gradient(circle, #B0C4DE 0%, transparent 70%)",
            left: "-10%",
            top: "10%",
            filter: "blur(60px)",
          }}
          animate={{
            scale: [1, 1.2, 1],
            x: [0, 30, 0],
          }}
          transition={{ duration: 20, repeat: Infinity, ease: "easeInOut" }}
        />
        <motion.div
          className="absolute w-80 h-80 rounded-full opacity-20"
          style={{
            background:
              "radial-gradient(circle, #D8BFD8 0%, transparent 70%)",
            right: "-10%",
            bottom: "20%",
            filter: "blur(60px)",
          }}
          animate={{
            scale: [1, 1.15, 1],
            y: [0, -20, 0],
          }}
          transition={{ duration: 25, repeat: Infinity, ease: "easeInOut" }}
        />
      </div>

      {/*  */}
      <motion.div
        className="relative z-10 w-full max-w-md px-6"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <div
          ref={containerRef}
          style={{ width: "100%", maxWidth: 420 }}
        />
      </motion.div>
    </div>
  );
}
